.register {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
  color: #fff;
}

.registerDiv {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.registerDiv form {
  width: 34%;
  padding: 80px 50px;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  position: relative;
  gap: 40px;
  background: #ffffff00;
  backdrop-filter: blur(1px);
  border: 1px solid #fff;
}

.registerDiv form > p {
  color: red;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding: 10px;
  background-color: #ff00001a;
  border-radius: 12px;
  border: 1px solid red;
}

.registerDiv form div {
  width: 100%;
  position: relative;
}
.registerDiv form div input {
  width: 100%;
  padding: 10px;
  outline: none;
  border: none;
  border-bottom: 1px solid #fff;
  font-size: 18px;
  color: #fff;
  background: transparent;
}

.registerDiv form div label {
  position: absolute;
  left: 5px;
  bottom: 45px;
  transition: 0.5s;
  font-size: 20px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri;
  font-family: "Inconsolata", monospace;
  pointer-events: none;
  user-select: none;
  color: #fff;
}

.btn-links {
  transform: translateY(20px);
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn-links a {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  color: #fff;
}

.btn-links button {
  width: 50%;
  background-color: transparent;
  padding: 10px;
  border-radius: 5px;
  color: #fff;
  font-size: 18px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  cursor: pointer;
}
.btn-links button:active {
  border: 2px solid #2b2b2b;
}

@media (max-width: 1250px) {
  .registerDiv form {
    width: 80%;
  }
}

@media (max-width: 500px) {
  .registerDiv form {
    width: 90%;
  }
}
